import React, { Component } from 'react'

class Chart extends Component {

  state = {
    activeChart: false,
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        activeChart: true
      })
    }, 200);
  }
  render() {
    return (
      <div className={this.state.activeChart ? "chart chart--active" : "chart"}>
        <h1 className={this.props.darkMode ? "title title--darkMode" : "title"} >covid-19 coronavirus <span>Charts</span></h1>
        <h3 className={this.props.darkMode ? "title title--darkMode temporary" : "title temporary"} >Chart Tab in progress...</h3>
      </div>
    )
  }
}
export default Chart
